.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color:black;
  color: white;
  text-align: center;
}

.nav-link {
  float: center;
  color: white;
  background-color: black;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 20px;
  margin: 10px;
  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

